@import '../../style/variables.scss';

.char {
    &__content {
        margin-top: 50px;
        display: grid;
        grid-template-columns: 650px 425px;
        column-gap: 25px;
        align-items: start;
    }
    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 200px);
        column-gap: 25px;
        row-gap: 30px;
    }
    &__list {
        text-align: center;
    }
    &__item {
        width: 200px;
        height: 318px;
        background-color: $dark;
        box-shadow: 5px 5px 10px rgba(0, 0, 0, .25);
        padding: 15px;
        cursor: pointer;
        transition: 0.3s transform;
        img {
            width: 200px;
            height: 200px;
            object-fit: cover;
            transform: translate(-15px, -15px);
        }
        &:focus{
            box-shadow: 0 5px 20px $main-color;
            transform: translateY(-8px);
        }
        
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
        text-transform: uppercase;
        color: #fff;
    }
}

.imgNotFound {
    object-fit: contain;
}

.enter {
    opacity: 0;
  }
.enter-active {
    opacity: 1;
    transition: all  500ms;
  }
.enter-done{
    opacity: 1;
    transition: all  500ms;
}
.exit {
    opacity: 1;
  }
.exit-active {
    opacity: 0;
    transition: all 500ms;
  }